</template>

<script>
import appFaqQuestion from '../components/Includes/FAQquestion'

export default {
    components: {
        appFaqQuestion
    },
    methods: {
        register(question) {
            this.$ga.event('Sign Up', 'Register Modal Opened', `${question} Question Link`);
            this.$store.dispatch('openModal', {component: 'register', data: {closeClass: 'white'}});
        },
        feedback(component) {
            this.$store.dispatch('openModal', {
                component: 'feedback', 
                data: {
                    closeClass: 'white',
                    subject: component
                }
            });
        }
    }
}
</script>




