<template>
	<div class="feedback-component">
		
		<div class="modal-header">
			<h3>Hr av dig</h3>
			<div class="header-text">Vi uppskattar all terkoppling.</div>
		</div>

        <div class="feedback-question-answer-container">
            <strong class="mb15 is-small-text" v-text="question.question" />
            <div class="gray-sub-text" v-text="question.answer" />
        </div>

		<div class="modal-body">
			<form @keydown="form.errors.clear()">

				<div class="alert alert-danger" v-if="errorMessage" v-text="errorMessage" />
                
                <div class="form-group" :class="{'has-error': form.errors.has('subject')}">
					<label for="subject" class="control-label">mne</label>
					<select class="form-control" v-model="form.subject">
                        <option value="categoryDontExist">Min kategori finns inte</option>
                        <option value="locationDontExist">Mitt omrde finns inte</option>
                        <option value="bugg">Har du hittat en bugg?</option>
                        <option value="help">Hjlp</option>
                        <option value="other">vrigt</option>
                    </select>

					<span class="help-block" v-if="form.errors.has('subject')" v-text="form.errors.get('subject')"></span>
				</div>
                
                <div class="form-group" :class="{'has-error': form.errors.has('email')}">
					<label for="email" class="control-label">Email</label>
					<input type="email" class="form-control" v-model="form.email">

					<span class="help-block" v-if="form.errors.has('email')" v-text="form.errors.get('email')"></span>
				</div>

				<div class="form-group" :class="{'has-error': form.errors.has('feedback')}">
					<label for="feedback" class="control-label">Feedback</label>
                    <textarea class="form-control" rows="4" v-model="form.feedback" />

					<span class="help-block" v-if="form.errors.has('feedback')" v-text="form.errors.get('feedback')"></span>
				</div>

			</form>
		</div>

		<div class="modal-footer">
			<button 
                type="submit"
                class="btn btn-primary"
                :class="{processing}"
                :disabled="processing"
                v-text="'Skicka feedback'"
                @click.prevent="send"
            />
		</div>

	</div>
</template>

<script>
	import Form from '../../includes/classes/Form';
    import Model from '../../includes/Model';
    import { mapGetters } from 'vuex';

	export default {
		data() {
			return {
                processing: false,
                errorMessage: '',
				form: new Form({
                    subject: '',
                    email: '',
					feedback: '',
                }),
                questions: {
                    categoryDontExist: {
                        question: 'Min kategori finns inte?',
                        answer: `Vi lgger hela tiden till nya kategorier. Vi vill se till att dr finns fretag som kan buda p en kategori 
                                innan vi lgger till den. Skicka grna frslag p kategorier som du vill att ska lgga till.`
                    },
                    locationDontExist: {
                        question: 'Mitt omrde finns inte?',
                        answer: `Vi r en nystartad plattform. Tanken r att vi inom kort ska tcka ett mycket strre omrde. Skicka grna frslag
                                p omrden som du vill att vi ska lgga till.`
                    },
                    bugg: {
                        question: 'Har du hittat en bugg?',
                        answer: `Frst och frmst: vi ber s mycket om urskt :). Vi jobbar hela tiden p att frbttra GoBid. Vi skulle verkligen
                                uppskatta om du kunde bertta fr oss var du har hittat en bugg s vi kan fixa den. Tack!`
                    },
                    help: {
                        question: 'Behver du hjlp?',
                        answer: `Vi finns hr fr dig om du behver hjlp. Skicka till oss vad du behver hjlp med s terkommer vi s fort vi kan.`
                    },
                    other: {
                        question: 'Vad har du p hjrtat?',
                        answer: `Har du ngot som du vill bertta fr oss? Tveka inte! Vi uppskatar att f terkoppling och kommer terkomma s fort vi kan.`
                    }
                }
			}
        },
        computed: {
            ...mapGetters({
                data: 'modalData'
            }),
            question() {
                return this.questions[this.form.subject];
            }
        },
		methods: {
            send() {
                this.processing = true;
                new Model('feedback').post(this.form.data())
                    .then(response => {
                        this.$store.dispatch('closeModal');
                        this.$store.dispatch('showNotification', {type: 'success', msg: 'Vi har tagit emot din feedback. Tack!'});
                    })
                    .catch(error => {
                        this.form.errors.record(error.errors);
                    })
                    .finally(() => this.processing = false );
            }
        },
        created() {
            this.form.subject = this.data.subject;
        }
	}
</script>


